<template>
  <div :class="messageClass">
    {{ message.text }}
    <div class="timestamp">{{ moment(message.created_at, "YYYYMMDD").fromNow() }} by {{ messageUser }} </div>
  </div>
</template>

<script>
export default {
  name: 'Message',
  props: {
    message: {
      type: Object,
      required: true
    },
  },
  computed: {

    messageUser() {

      if (this.isMine) {
        return 'You'
      }

      return this.message.user.name
    },

    isMine() {

      if (this.message.user.uuid === this.$store.getters.currentUser.uuid) {
        return true
      }

      return false

    },

    messageClass() {
      let class_name = 'message'

      if (this.isMine) {
        class_name = class_name + ' ' + 'message-mine'
      }

      return class_name
    },

  }
}
</script>

