<template>
  <fragment>
    <section class="messages" ref="message_section">
      <div class="messages-content">
        <Message v-for="message in messages" :message="message" :key="message.uuid" ></Message>

        <h3 v-if="!messages.length">No message yet. Start chatting now!</h3>
      </div>
    </section>
    <section class="message-box">
      <form @submit.prevent="handleSubmit">
        <input type="text" v-model="message_text" class="message-input" placeholder="Type message...">
        <button type="submit" class="message-submit">SEND</button>
      </form>
    </section>
  </fragment>
</template>

<script>

import { mapState, mapActions } from 'vuex'

import Message from '../components/Message.vue'

export default {
  name: 'Login',
  components: {
    Message
  },
  computed: {
    ...mapState([
      'messages',
    ]),
  },
  data() {
    return {
      message_text: null
    }
  },
  methods: {

    ...mapActions([
      'sendMessage',
    ]),

    scrollToLatestMessage() {
      let container = this.$refs.message_section;
      container.scrollTop = container.scrollHeight;
    },

    // handle form submission
    async handleSubmit() {

      // check if empty
      if (!this.message_text) {
        return
      }

      try {

        await this.sendMessage(this.message_text)

        // clear message input
        this.message_text = null

        // scroll to bottom of message list
        this.scrollToLatestMessage()
      }
      catch (error) {
        console.log(error)
      }

    },

  }
}
</script>
